* {
    padding: 0;
    margin: 0;
    font-family: 'Times New Roman';
    box-sizing: border-box;
}

.body_About{
    background-color: rgb(250, 240, 215);
    width: 100%;
}
.body_About h1{
    text-align: center;
}
.About_us{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.About_us_main{
    width: 80%;
    border: 5px solid orangered;
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
}
.head_about_us{
    margin-top: 10px;
    width: 100%;
    display:flex;
    flex-direction: column;
    align-items: center;
}
.head_about_us img{
    height:200px;
    width:50%;
}
.Tag{
    width: 50%;
    font-size: 22px;
    background-color: rgb(234, 189, 112);
    margin-top: 10px;
    text-align: center;
}
.content_about_us{
    position: relative;
    width: 80%;
    margin-top: 50px;
    font-size: 120%;
    margin-bottom: 100px;
}
.content_about_us h4{
    color:rgb(5, 117, 125);
    padding-left: 5%;
    margin-top: 30px;
}
#hr_about{
    width: 100%;
    border: 2px solid black; 
    border-color: rgb(158, 159, 165);
    margin-top: 20px; 
    margin-bottom: 20px;
}
.content_about_us li::before {
    content: "●";
    font-size: 120%; 
    font-weight: bold; 
    color: black; 
    position:absolute;
    transform: translateX(-400%);
  }

  .content_about_us li {
    margin-left: 10%;
    margin-top: 10px;
  }
.About_us .social{
    text-decoration: none;
    color:rgb(255, 47, 10)
}