.footer-main {
    max-width: 100%;
    margin: auto;
  }
  ul {
    list-style: none;
  }
  .footer {
    background-color: #24262b;
    padding: 70px 0;
    border-top: 3px solid rgb(57, 218, 230);
  }
  .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .footer-col {
    width: 20%;
    padding: 3%;
  }
  .footer-col h4 {
    font-size: 18px;
    color: #fff;
    text-transform: capitalize;
    margin-bottom: 35px;
    font-weight: 500;
    position: relative;
  }
  .footer-col h4::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -10px;
    background-color: #e91e63;
    height: 2px;
    box-sizing: border-box;
    width: 50px;
  }
  .footer-col ul li:not(:last-child) {
    margin-bottom: 10px;
  }
  .footer-col ul li a {
    width: 100%;
    font-size: 17px;
    text-decoration: none;
    font-weight: 300;
    color: #bbb;
    display: block;
    transition: all 0.3s ease;
  }
  .footer-col ul li a:hover {
    color: #e91e63;
    padding-left: 8px;
  }
  .footer-col .social-links a {
    display: inline-block;
    height: 40px;
    width: 40px;
    background-color: rgba(255, 255, 255, 0.2);
    margin: 0 10px 10px 0;
    text-align: center;
    line-height: 40px;
    border-radius: 50px;
    color: aliceblue;
    transition: all 0.5s ease;
  }
  .footer-col .social-links a:hover {
    background-color: #f62682;
  }
  .imglogo {
    width: 25%;
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
    /* margin-right: 5%; */
  }
  .imglogo img {
    width: 50%;
    height: 150px;
  }